:root {
  --background-top-color: #009688;
  --background-top-height: 125px;
  --background-mid-color: #d9dbd5;
  --content-main-background-color: #f8f9fb;
  --content-dialogs-background-color: #daba9b;
  --intro-secondary: rgba(0, 0, 0, 0.45);
  --header-control-background: #d5d5d5;
  --message-incoming-background: white;
  --message-outgoing-background: #dcf8d6;
  --page-shadow-rgb: rgb(162, 162, 162);
  --page-margin-top: 19px;
  --page-margin-top-bottom: 38px;
  --profile--main--color: #00bfa5;
  --sidebar-background-color: white;
  --sidebar-border-color: #e4e4e4;
  --sidebar-active-dialog-background: #ebebeb;
  --sidebar-dialog-border-color: #dddddd8e;
  --sidebar-header-background: #ededed;
  --sidebar-search-background: #f6f6f6;
  --sidebar-width-30: 30%;
  --sidebar-width-35: 35%;
  --sidebar-width-40: 40%;
  --scroll-track-color: #f6f6f6;
  --scroll-thumb-color: #dfdbdb;
  --search-container-background: #f6f6f6;
  --text--light--grey--color: grey;
  --text--draft--color: #c05f5a;
}

* {
  margin: 0;
  padding: 0;
}

html {
  box-sizing: border-box;
}
*, *::before, *::after {
  box-sizing: inherit;
  letter-spacing: .8px;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  font-size: 16px;
  line-height: 1.2;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*::-webkit-scrollbar {
  width: 7px;
}
*::-webkit-scrollbar-track {
  background: #f6f6f6;
  background: var(--scroll-track-color);
}
*::-webkit-scrollbar-thumb {
  background-color: #dfdbdb;
  background-color: var(--scroll-thumb-color);
  border-radius: 20px;
}
.page__background {
  width: 100%;
  height: 100vh;
  position: relative;
  background-color: lightgray;
}

.page__background .page__background_color:first-child {
  display: block;
  content: '';
  width: 100vw;
  height: var(--background-top-height);
  background-color: var(--background-top-color);
  z-index: 1;
}

.page__background .page__background_color:nth-child(2) {
  display: block;
  content: '';
  width: 100vw;
  height: calc(100vh - var(--background-top-height));
  background-color: var(--background-mid-color);
  z-index: 1;
}
.page__wrap {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
	width: 1400px;
	height: 400px;
  background-color: white;
  margin-top: var(--page-margin-top);
  height: calc(100vh - var(--page-margin-top-bottom));

  display: grid;
  grid-template-columns: 30% 70%;

  box-shadow: 
    4px 4px 12px 0 var(--page-shadow-rgb),
    0px 0px 0px 0 var(--page-shadow-rgb);
    
    overflow: hidden;
}

@media only screen and (max-width: 1440px) {
  .page__wrap {
    width: 100vw;
    height: 100vh;
    margin-top: 0;
  }
}

@media only screen and (max-width: 1300px) {
  .page__wrap {
    grid-template-columns: 35% 65%;
  }
}

@media only screen and (max-width: 900px) {
  .page__wrap {
    grid-template-columns: 40% 60%;
  }
}
.page__content_home {
  background-color: var(--content-main-background-color);
  display: flex;
  justify-content: center;
  align-items: center;
  display: flex;
  flex-direction: column;
  text-align: center;
  color: var(--intro-secondary);
  border-left: 1px solid var(--sidebar-border-color);
}

.page__content_home h2 {
  width: 460px;
  font-weight: 300;
  font-size: 36px;
  margin-bottom: 24px;
}

.page__content_home p {
  width: 460px;
  
  line-height: 22px;
}
.activedialog__wrap {
  display: flex;
  flex-direction: column;
  
  border-left: 1px solid var(--sidebar-border-color);
}

/* ------------------------Header------------------------ */
.activedialog__header {
  height: 59px;
  padding: 9px 16px;
  background-color: var(--sidebar-header-background);
  border-bottom: 1px solid var(--sidebar-border-color);

  display: flex;
  justify-content: space-between;
}

.header__user {
  display: flex;
  width: 100%;
  cursor: pointer;
}

.header__user_avatar {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  margin-right: 16px;
  background: center / contain no-repeat url(/whatsApp-demo/static/media/avatar-default.57a0f3bd.png);
  /* background-color: var(--background-top-color) !important; */
  background-color: transparent !important;
}

.header__user_info {
  display: flex;
  flex-direction: column;
}

.header__user_name {
  height: 20px;
  font-size: 16px;
}
.header__user_seen {
  height: 20px;
  width: 100%;
  font-size: 13px;
  display: flex;
  align-items: flex-end;
  line-height: 20px;
  opacity: .4;
}

.header__control {
  display: flex;
}
.header__control div {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  margin-left: 10px;
  opacity: .5;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

.header__control div:active {
  background-color: var(--header-control-background);
}

/* ------------------------Content------------------------ */
.activedialog__content {
  width: 100%;
  height: 100%;
  position: relative;
}
.activedialog__content_background {
    width: 100%;
    height: 100%;
    background-image: url(/whatsApp-demo/static/media/wallpaper-min.d33f7f11.png);
    opacity: .7;
}
.activedialog__content_background::after {
  display: block;
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: var(--content-dialogs-background-color);
  opacity: .5;
}

.content__main {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;

  display: flex;
  flex-direction: column-reverse;

  overflow-y: auto;
  /* float: inline-end; */
}

.content__main_messages {
  display: flex;
  flex-direction: column-reverse;
  margin: auto 0 0;
  padding: 20px 9% 8px 9%;
}

.content__message {
  margin-bottom: 12px;
  padding: 6px 7px 8px 9px;
  max-width: 65%;
  border: 1px solid var(--sidebar-border-color);
  border-radius: 9px;
}

.content__message.incoming {
  float: left;
  align-self: flex-start;
  background-color: var(--message-incoming-background);
}

.content__message.outgoing {
  float: right;
  align-self: flex-end;
  background-color: var(--message-outgoing-background);
}

.content__message_text {
  word-wrap: break-word;
}

@media only screen and (max-width: 1300px) {
  .content__message {
    max-width: 75%;
  }
}
@media only screen and (max-width: 1024px) {
  .content__message {
    max-width: 85%;
  }
}
@media only screen and (max-width: 900px) {
  .content__message {
    max-width: 95%;
  }
}

/* ------------------------ Footer ------------------------ */
.activedialog__footer {
  /* height: 70px; */
  /* height: auto; */
  background-color: var(--sidebar-header-background);
  margin-top: auto;
  display: flex;
  align-items: center;
  padding: 9px 0px 9px 0;
}

.footer__emoji {
  width: 52px;
  height: 52px;
  display: flex;
  justify-content: center;
  align-items: center;
  opacity: .5;
  cursor: pointer;
}

/* ------------------------ Form ------------------------ */
.input__form {
  width: 90%;
}

.input__form_text {

  display:inline-block;
  background-color: white;
  border: 0;
  width: 100%;
  max-height: 70px;
  overflow-x: hidden;
  overflow-y: auto;
  height: auto;    
  margin: 5px 10px;
  padding: 9px 15px 11px;
  border-radius: 21px;
}
.input__form_text:empty:before {
  content: attr(data-placeholder);
  opacity: .5;
}

/* ------------------------ Media ------------------------ */
@media only screen and (max-width: 1100px) {
  .input__form_text {
    width: 98%;
  }
}

@media only screen and (max-width: 950px) {
  .input__form_text {
    width: 95%;
  }
}
